import { Marker } from './Marker';
import { LineStyle, MarkerShapes } from './Shapes.types';
import { Line } from './Line';
import { ColorProps } from '../utils/UNSAFE_interpolations/colors';
import { styles } from './themes/SvgShapesStyles.css';

type SymbolProps = {
  markerShape: MarkerShapes | 'none';
  lineStyle: LineStyle | 'none';
  lineWidth?: number;
  lineColor?: ColorProps['color'];
  borderColor?: ColorProps['color'];
  markerColor?: ColorProps['color'];
  setAspectRatioNone?: boolean;
  lineLength?: number;
};

const MARKER_SHAPE_RATIO = 0.6;
const MAX_SCALE = 1;
export const SvgSymbol = ({
  markerShape,
  lineStyle = 'none',
  lineLength,
  lineWidth,
  lineColor,
  borderColor,
  markerColor,
  setAspectRatioNone
}: SymbolProps) => {
  const markerScale = lineStyle != 'none' ? MARKER_SHAPE_RATIO : MAX_SCALE;
  return (
    <svg
      width="100%"
      height="100%"
      viewBox="0 0 100 100"
      class={styles.svgSymbolBaseStyle}
      preserveAspectRatio={setAspectRatioNone ? 'none' : undefined}>
      {lineStyle != 'none' && (
        <Line
          lineStyle={lineStyle}
          lineWidth={lineWidth}
          color={lineColor}
          lineLength={lineLength}
        />
      )}

      {markerShape != 'none' && (
        <Marker
          type={markerShape}
          scale={markerScale}
          height={100}
          width={100}
          preserveRectAspectRatio={!setAspectRatioNone}
          fill={markerColor}
          stroke={borderColor}
        />
      )}
    </svg>
  );
};
